<template>
  <div class="position-relative wrapper">
    <div class="position-absolute bg" ></div>
    <div class="position-absolute content">
      <slot name="content"></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AuthLayout',
  props: {
    backImg: {
      type: String,
      default: 'login.jpg'
    }
  }
}
</script>

<style scoped lang="sass">
@import "@/common/sass/base.sass"
body
  overflow: hidden

.wrapper, .bg, .content
  width: 100%
  padding: 0
  margin: 0
  display: block
  height: 100vh

.bg, .content
  top: 0
  left: 0

.bg
  // background: url('~@/assets/login.jpg') center center no-repeat #fff
  background-clip: border-box
  background-position: center
  background-repeat: no-repeat
  background-size: cover

</style>


