<template>
  <form class="form form-panel text-left of-visible">
    <div class="form-group of-visible">
      <!-- label for="email">電郵</label -->
      <input type="text" class="form-control" id="email" placeholder="電郵地址" v-model="email">
      <small id="emailTip" class="form-text text-muted">{{ tip_email }}</small>
    </div>
    <div class="form-group of-visible">
      <!-- label for="password">密碼</label -->
      <input type="password" class="form-control" id="password" placeholder="密碼" v-model="password" @keyup.enter="submitLoginForm">
      <small id="passwordTip" class="form-text text-muted">{{ tip_password }}</small>
    </div>
  </form>

</template>

<script>
import conn from '@/service/conn'
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import validate from '@/common/es/validate'

export default {
  name: 'LoginForm',
  data() {
    return {
      password: '',
      email: '',

      tip_email: '',
      tip_password: ''
    }
  },
  methods: {
    showMsg(msg, t) {
      if (msg == 'PWD_ERROR') {
        msg = '密码错误！！！' 
      } else if (msg == 'NO_USER') {
        msg = '无此账号！！！'
      }
      tool.showToast(msg, t)
    },

    async _submit(t = 1800) {
      let res = await conn.user.login(this.email, this.password)
      res = res.data

      if (res.status) {
        tool.showToast('登录成功。', t)
        
        await this.$store.commit('saveLogin', {
          nick: res.nick,
          named: this.email,
          pass: tool.salt.add_salt_form_string(this.password)
        })
        
        setTimeout(() => {
          this.$router.push({ path: '/' })
        }, 918)
        
      } else {
        this.showMsg(res.msg, t)
      }
    },
    submitLoginForm() {
      const validate = this.validateForm()
      if (validate == true) {
        this._submit()
      } else {
        tool.showToast('請檢查輸入')
      }
    },
    // 
    tipClose(block) {
      setTimeout(() => {
        if (block == 'email') { this.tip_email = '' }
        else if (block == 'password') { this.tip_password = '' }
      }, 4200)
    },
    tipForm(res, block) {
      let show = ''
      if (res != true) {
        show = res
      }
      // 
      if (block == 'email') { this.tip_email = show }
      else if (block == 'password') { this.tip_password = show }

      this.tipClose(block)
    },
    validateForm() {
      let res = validate.val_empty(this.email, '電郵')
      if (res == true) {
        res = validate.val_email(this.email, '電郵')
        this.tipForm(res, 'email')
      } else { this.tipForm(res, 'email') }

      res = validate.val_empty(this.password, '密碼')
      if (res == true) {
        res = validate.val_char(this.password, '密碼')
        this.tipForm(res, 'password')
      } else { this.tipForm(res, 'password') }

      return res
    }
  }
}
</script>

<style scoped lang="sass">
@import "@/common/sass/base.sass"

.form-panel
  margin: 0
</style>


