<template>
  <div class="row top-bar p-0" :class="{ 'opacityBar': (opacity > 0) }">
    <div class="col-sm-8"></div>
    <div class="col-sm-4 text-right top-bar-right-content">
      
      {{ $store.getters.getUserName() }}
    </div>
  </div>
</template>

<script>
export default {
  name: 'TopBar',
  props: {
    opacity: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      email: 'support@manfulls.com'
    }
  }
}
</script>

<style scoped lang="sass">
@import "@/common/sass/base.sass"
.opacityBar
  opacity: 0.79

.top-bar
  width: 100vw
  z-index: 999
  padding: 0 !important
  margin: 0
  overflow: auto

.top-bar > div
  background: $scaffold
  color: rgba(255, 255, 255, 0.7)
  height: $topBarH

.top-bar-right-content
  padding-right: $topBarRightPadding
  display: flex
  align-items: center
  justify-content: flex-end
  vertical-align: middle
  height: $topBarH
  overflow: auto

.tbd-panel
  z-index: 999
</style>


