<template>

  <auth-layout :backImg="'login.jpg'">

    <div class="bg-hui" slot="content">

      <!-- 頂部菜單 --> 
      <scaffold-bar class="position-fixed"></scaffold-bar>

      <div class="container-fluid">
        
        <div class="row d-flex justify-content-center wrapper">

          <!-- 左菜單 --> 
          <div class="col-sm-12 col-lg-4 pt-5">
            <h1 class="login-title">&nbsp;</h1>
            <div class="login-panel bg-white def-shadow mt-2 text-center of-visible">
              <login-form ref="form"></login-form>
              <button class="btn btn-outline-info login-btn" @click="submit" @:keyup.enter="submit">登錄</button>
            </div>
          </div>
          
        </div>
      </div>

      <!-- 提示 -->
      <toast></toast>
    </div>

  </auth-layout>
</template>

<script>
import ScaffoldBar from '@/components/TopBar/ScaffoldBar'
import Toast from '@/components/Dialog/Toast.vue'
import LoginForm from './LoginForm.vue'
import AuthLayout from '@/components/Layout/AuthLayout.vue'
export default {
  name: "LoginView",
  components: {
    ScaffoldBar,
    Toast,
    LoginForm,
    AuthLayout
  },
  methods: {
    submit() {
      this.$refs.form.submitLoginForm()
    }
  }
}
</script>

<style scoped lang="sass">
@import "@/common/sass/base.sass"

.login-title
  color: $scaffold
  margin-top: 109px

.container-fluid
  padding-left: 0
  padding-right: 0
  z-index: 80

.wrapper
  height: 100vh
  padding-top: $topBarH !important

.login-panel 
  min-height: 60px
  border-radius: $horizon
  padding: $horizon

.login-btn
  padding-left: $horizonXL
  padding-right: $horizonXL
  margin-bottom: $horizonS
</style>


